import React, { useState, useEffect, useLayoutEffect } from 'react';
import styled from '@emotion/styled';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import { css } from '@emotion/react'
import { useTheme } from '@mui/material';

import ContentTitle from '../../components/Layout/ContentTitle';
import { MainCard, SubCard } from '../../components/Card';
import { HistoryDataTable } from '../../components/Table';
import { Loader } from '../../components/Loader';
import { Chart, DEFAULT_OPTIONS } from '../../components/Chart';
import Icon from '../../assets/icons';

import { useSnackbar } from 'notistack';
import { useGetAllModelListQuery } from '../../service/models';

import Api from '../../service/api';
import { useAuth } from '../../hooks/useAuth';
import { sleep } from '../../utils';
import { useNavigate } from 'react-router-dom';

const HorizontalStack = styled('div')(({ theme }) => ({
  display: 'grid',
  rowGap: '16px',
  columnGap: '16px',
  alignItems: 'start',
  [theme.breakpoints.up('md')]: {
    gridTemplateColumns: '1fr 1fr',
  },
  [theme.breakpoints.down('md')]: {
    gridTemplateRows: '1fr 1fr',
  },
}));

const SelectBox = styled('div')`
  width: 100%;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  & > div:first-of-type {
    width: 300px;
    margin-right: 16px;
  }
  & button {
    margin-left: auto;
  }
  & > span {
    white-space: nowrap;
    margin-right: 8px;
  }
`;

const getTimeNow = () => Date.now();

const getNewSeries = (data, options) => {
  const nextData = data.at(-1).y + options.step
  const newData = nextData < options.max ? nextData : options.max
  return [...data, { x: getTimeNow(), y: newData }]
}

const getStep = (min, max) => {
  if (min === max) {
    return Math.round(Math.random() * min)
  } else if (min < max) {
    return Math.round(Math.random() * (max - min) + min)
  } else {
    return Math.round(Math.random() * (min - max) + min)
  }
}

const RealtimeChart = () => {
  const CHART_HEIGHT = 260;
  const TICK_INTERVAL = 500;
  const SERIES_RANGE = 9;
  const X_AXIS_RANGE = TICK_INTERVAL * SERIES_RANGE;

  const theme = useTheme()
  const [data, setData] = useState([])

  useLayoutEffect(() => {
    const firstData = Array(SERIES_RANGE + 1)
      .fill({ x: getTimeNow(), y: 0 })
      .map((item, idx, arr) => {
        const gap = TICK_INTERVAL * (arr.length - idx)
        return {
          ...item,
          x: item.x - gap
        }
      })
    setData(firstData)
  }, [setData])
  
  // useEffect(() => console.log('data', data), [data])
  
  const series = [{
    // copy the data
    data: data.slice()
  }]
  
  // + optional annotation
  const annotationOptions = {
    // x: [x of last data item] || [initial value]
    x: data.at(-1)?.x || getTimeNow(),
    strokeDashArray: 1,
    borderColor: theme.palette.primary.main,
    label: {
      borderColor: theme.palette.primary.main,
      style: {
        color: '#fff',
        background: theme.palette.primary.main,
        fontSize: '12px',
        fontWeight: 500,
        fontFamily: 'inherit',
      },
      offsetY: CHART_HEIGHT - 64, // baseline of xaxis label
      orientation: 'horizontal',
      textAnchor: 'end',
      text: data.at(-1)?.y.toString() | '0',
    }
  }
  
  const options = {
    ...DEFAULT_OPTIONS,
    annotations: {
      xaxis: [
        annotationOptions
      ]
    },
    chart: {
      ...DEFAULT_OPTIONS.chart,
      id: 'realtime',
      type: 'line',
      animations: {
        enabled: true,
        easing: 'linear',
        dynamicAnimation: {
          speed: 1000
        }
      },
    },
    stroke: {
      colors: [theme.palette.primary.main],
      lineCap: 'round',
    },
    title: {
      text: 'Averaging Progress',
      align: 'left'
    },
    grid: {
      row: {
        colors: [theme.palette.grey[50], 'transparent'],
        opacity: 0.5
      },
    },
    xaxis: {
      type: 'datetime',
      range: X_AXIS_RANGE,
      axisBorder: {
        show: false,
      },
      axisTicks: {
        show: false,
      },
      crosshairs: {
        show: false,
      },
    },
    yaxis: {
      min: 0,
      max: 100,
      tickAmount: 5,
      crosshairs: {
        show: false,
      },
    },
    legend: {
      show: false,
    },
    tooltip: {
      enabled: false,
      intersect: false,
      onDatasetHover: {
        highlightDataSeries: false,
      },
      x: {
        show: false
      },
      marker: {
        show: false
      },
    },
  }

  useEffect(() => {
    const updateChart = setInterval(() => {      
      setData((dataPrev) =>
        getNewSeries(dataPrev, { step: getStep(8, 20), max: 100 })
      )
    }, TICK_INTERVAL)

    return () => {
      clearInterval(updateChart)
    }
  })
  
  return (
    <div id="chart">
      <Chart
        type='line'
        height={CHART_HEIGHT}
        options={options}
        series={series}
      />
    </div>
  )
}

export default function Averaging() {
  const navigate = useNavigate();
  const { session } = useAuth();
  const { enqueueSnackbar } = useSnackbar();
  
  const { data, error, isLoading, isFetching, refetch } = useGetAllModelListQuery();
  
  const initialAlgorithm = 'SMC_ALGO';
  const [algorithm, setAlgorithm] = useState(initialAlgorithm);
  const rowsByAlgo = data?.filter(({ algo }) => algo === algorithm) || [];
  const rowsByHos = {
    SMC: rowsByAlgo.filter(({ hos }) => hos === "SMC"),
    KUMC: rowsByAlgo.filter(({ hos }) => hos === "KUMC"),
  }

  const [isAveraging, setIsAveraging] = useState(false);

  const initialSelected = {
    SMC: [],
    KUMC: []
  };
  const [selectedRow, setSelectedRow] = useState(initialSelected);

  // useEffect(() => console.log(selectedRow), [selectedRow])

  const getSelectedRows = (id, selected) => {
    setSelectedRow({
      ...selectedRow,
      [id]: selected
    })
  }
  
  const handleSelectAlgorithm = (event) => {
    setAlgorithm(event.target.value)
    setSelectedRow(initialSelected)
  }
  
  const handleRefresh = () => {
    refetch();
  }
  
  const handleAveraging = async (event) => {
    if (selectedRow?.SMC.length !== 1 || selectedRow?.KUMC.length !== 1) {
      enqueueSnackbar(
        '각 기관에서 하나의 모델을 선택한 후 병합을 진행하세요.',
        {
          key: 'wrong-selected',
          variant: 'error',
        }
      );
      return;
    }
    
    const validSession = await session();
    if (!validSession.user) {
      alert('세션이 만료되었습니다. 재로그인 하시기 바랍니다.')
      navigate('/login', { replace: true })
      return;
    }

    const key = event.timeStamp;
    const delay = 1200;
    
    // const queryString = `Hos1=SMC&Name1=9767.h5&Hos2=KUMC&Name2=model_v1.h5` // query-string for msw service
    const [Name1, Name2] = Object.values(selectedRow).map(rows => rows.at(0).name)
    const queryString = `Hos1=SMC&Name1=${Name1}&Hos2=KUMC&Name2=${Name2}&Algo=${algorithm}`
    
    Api.get(
      `/average?${queryString}`,
    ).then((response) => {
        console.clear()
        //console.log('averaging response', response)
        setIsAveraging(true) // 1️⃣ Show Realtime chart
        setSelectedRow(initialSelected) // reset selected table row
      })
      .then(async (data) => {
        //console.log('response data: ', data)
        await sleep(5000 + delay)
        setIsAveraging(false) // 2️⃣ Finish update and Hide Realtime chart
        enqueueSnackbar(
          data?.message || '병합이 성공적으로 완료되었습니다.',
          {
            key,
            variant: 'success',
          }
        );
      })
      .catch((error) => {
        //console.log('error', error)
        enqueueSnackbar(
          `error: ${error?.message || '병합에 실패하였습니다'}`,
          {
            key,
            variant: 'error',
          }
        );
      })

  }

  if (error) return <div>error: {error}</div>;

  return (
    <>
      <ContentTitle
        title="모델 애버리징"
        pageAction={(
          <Button
            disabled={isLoading || isFetching}
            color="secondary"
            startIcon={(
              <span
                className={css`
                  margin-right: 0;
                  line-height: 0;
                  svg {
                    animation: ${(isLoading || isFetching) ? 'spin 1s ease-in-out infinite;' : 'none'}
                  }
                `}
              >
                <Icon.Refresh stroke={2} />
              </span>
            )}
            onClick={handleRefresh}
          >테이블 새로고침</Button>
        )}
      />
      <div>
        <SelectBox>
          <Select
            value={algorithm}
            fullWidth
            size="small"
            color="primary"
            variant="standard"
            onChange={handleSelectAlgorithm}
          >
            <MenuItem value="SMC_ALGO">응급실 중증환자 처방 추천 모델</MenuItem>
            <MenuItem value="KUMC_ALGO">응급실 심혈관 시술 및 수술 예측 모델</MenuItem>
          </Select>
          <Button
            disabled={isAveraging}
            color="primary"
            variant="contained"
            onClick={handleAveraging}
          >모델 병합</Button>
        </SelectBox>
        {isAveraging ? (
            <MainCard sx={{ mb: 2 }}>
              <RealtimeChart />
            </MainCard>
          ) : null
        }
        {(isLoading || isFetching || !data)
          ? <Loader />
          : <HorizontalStack>
            <MainCard>
              <SelectBox>
                <Typography variant="subtitle2" component="span" color="primary.main">기관명</Typography> 
                <TextField
                  value="삼성서울병원"
                  fullWidth
                  size="small"
                  inputProps={{
                    readOnly: true
                  }}
                />
              </SelectBox>
              <SubCard>
                {/* history list */}
                <HistoryDataTable
                  tableId="SMC"
                  title="모델 업데이트 히스토리"
                  bodyHeight="400px"
                  rows={rowsByHos["SMC"]}
                  checkboxSelection
                  selected={selectedRow.SMC}
                  setSelected={getSelectedRows}
                />
              </SubCard>
            </MainCard>
            <MainCard>
              <SelectBox>
                <Typography variant="subtitle2" component="span" color="primary.main">기관명</Typography> 
                <TextField
                  value="고대안암병원"
                  fullWidth
                  size="small"
                  inputProps={{
                    readOnly: true
                  }}
                />
              </SelectBox>
              <SubCard>
                {/* history list */}
                <HistoryDataTable
                  tableId="KUMC"
                  title="모델 업데이트 히스토리"
                  bodyHeight="400px"
                  rows={rowsByHos["KUMC"]}
                  checkboxSelection
                  selected={selectedRow.KUMC}
                  setSelected={getSelectedRows}
                />
              </SubCard>
            </MainCard>
        </HorizontalStack>
      }
      </div>
    </>
  )
}