import React from 'react';
import styled from '@emotion/styled';
import { css } from '@emotion/css';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

import ContentTitle from '../../components/Layout/ContentTitle';
import { MainCard, SubCard } from '../../components/Card';
import { HistoryDataTable } from '../../components/Table';
import { Loader } from '../../components/Loader';
import Icon from '../../assets/icons';

import { useGetAllModelListQuery } from '../../service/models';
import { useSnackbar } from 'notistack';
import Api from '../../service/api';
import { useAuth } from '../../hooks/useAuth';
import { useNavigate } from 'react-router-dom';

const SelectBox = styled('div')`
  width: 100%;
  display: flex;
  align-items: center;
  & > div:first-of-type {
    width: 300px;
    margin-right: 16px;
  }
  & button {
    margin-left: auto;
  }
  & > span {
    white-space: nowrap;
    margin-right: 8px;
  }
`;

export default function Download() {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  let { user, session } = useAuth();
  const hos = user.type.toUpperCase();
  const initialAlgorithm = `${hos}_ALGO`
  const [algorithm, setAlgorithm] = React.useState(initialAlgorithm);

  const { data, error, isLoading, isFetching, refetch } = useGetAllModelListQuery();
  const rows = data?.filter(({ hos }) => hos === "CNT") || [];
  const rowsByAlgo = (_algorithm) => rows.filter(({ algo }) => algo === _algorithm)
  
  // NOTE: [{ id: number, name: string, date: string, size: number, acu: number, rec: number, pre: number, f1: number }]
  const initialSelected = {
    SMC_ALGO: [],
    KUMC_ALGO: [],
  };
  const [selectedRow, setSelectedRow] = React.useState(initialSelected)
  
  const getSelectedRows = (id, selected) => {
    setSelectedRow({
      ...selectedRow,
      [id]: selected
    })
  }
  
  const handleDownloadModel = async (algo) => {    
    if (typeof selectedRow[algo]?.length !== 'number' && selectedRow[algo]?.length <= 0) {
      enqueueSnackbar(
        '선택된 파일이 없습니다.',
        {
          key: 'no-selected',
          variant: 'error',
          preventDuplicate: true
        }
      )
      return;
    }

    setSelectedRow(initialSelected);

    const validSession = await session();
    if (!validSession.user) {
      alert('세션이 만료되었습니다. 재로그인 하시기 바랍니다.')
      navigate('/login', { replace: true })
      return;
    }
      
    selectedRow[algo].forEach(async (item, index) => {
      Api.get(`/files/${algo}/CNT/${item.name}`,
      {
        responseType: 'blob'
      }).then(response => {
        //console.log('download response', response)
        const href = URL.createObjectURL(response.data);

        const link = document.createElement('a');
        link.href = href;
        link.setAttribute('download', item.name); 
        document.body.appendChild(link);
        link.click();

        document.body.removeChild(link);
        URL.revokeObjectURL(href);
      })
      .then(data => {
        //console.log('response data: ', data)
      })
      .catch(error => {
        //console.log('error', error)
        enqueueSnackbar(
          '다운로드에 실패하였습니다.',
          {
            key: 'download-error',
            variant: 'error',
            preventDuplicate: true
          }
        )
      })
    });

  }

  const handleDownloadLocalModel = (event) => {
    event.preventDefault();
    handleDownloadModel(algorithm)
  }

  const handleRefresh = () => {
    refetch();
  }
  
  if (error) return <div>error: {error}</div>;

  return (
    <>
      <ContentTitle
        title="모델 다운로드"
        pageAction={(
          <Button
            disabled={isLoading || isFetching}
            color="secondary"
            startIcon={(
              <span
                className={css`
                  margin-right: 0;
                  line-height: 0;
                  svg {
                    animation: ${(isLoading || isFetching) ? 'spin 1s ease-in-out infinite;' : 'none'}
                  }
                `}
              >
                <Icon.Refresh stroke={2} />
              </span>
            )}
            onClick={handleRefresh}
          >테이블 새로고침</Button>
        )}
      />
      <div className="content-area" style={{ minHeight: 'calc(100vh - 260px)' }}>
        {(isLoading || isFetching || !data)
          ? <Loader fullHeight />
          : <MainCard>
            <SelectBox>
              <Typography variant="subtitle2" component="span" color="primary.main">모델명</Typography>
              <Select
                value={algorithm}
                fullWidth
                size="small"
                color="primary"
                variant="standard"
                onChange={(event) => setAlgorithm(event.target.value)}
              >
                <MenuItem value="SMC_ALGO">응급실 중증환자 처방 추천 모델</MenuItem>
                <MenuItem value="KUMC_ALGO">응급실 심혈관 시술 및 수술 예측 모델</MenuItem>
              </Select>
              <Button
                variant="contained"
                color="primary"
                size="medium"
                startIcon={<Icon.Download />}
                sx={{ marginLeft: 'auto' }}
                onClick={(event) => handleDownloadLocalModel(event)}
              >다운로드</Button>
            </SelectBox>
            <SubCard>
              {/* history list */}
              {isLoading
                ? <Loader />
                : <HistoryDataTable
                  tableId={algorithm}
                  title="모델별 업데이트 히스토리"
                  bodyHeight="400px"
                  rows={rowsByAlgo(algorithm)}
                  checkboxSelection
                  selected={selectedRow[algorithm]}
                  setSelected={getSelectedRows}
                />
              }
            </SubCard>
          </MainCard>
        }
      </div>
    </>
  );
};
