import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Link from '@mui/material/Link';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import styled from '@emotion/styled';

import { useAuth } from '../hooks/useAuth';
import Logo from '../components/Layout/Logo';


const StyledLoginBox = styled('div')(({ theme }) => ({
  background: theme.palette.background.paper,
  width: '750px',
  minHeight: '580px',
  padding: '96px 130px 80px',
  '.mail-to-help': {
    marginTop: '60px',
    textAlign: 'center',
    fontFamily: "'SpoqaHanSansNeo'"
  },
  [theme.breakpoints.up('md')]: {
    margin: '140px auto',
    borderRadius: '16px',
    boxShadow: theme.shadows[9]
  },
  [theme.breakpoints.down('md')]: {
    width: '100%',
    minHeight: '100vh',
    minWidth: '556px',
    margin: '0px',
    padding: '96px 14.44vw',
  },
}))

const StyledForm = styled('form')`
  width: 100%;
  display: inline-flex;
  flex-direction: column;
  position: relative;
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
  vertical-align: top;
  width: 100%;
  & > div { 
    padding-bottom: 16px;
    &:not(:last-of-type) {
      margin-bottom: 16px;
    }
  }
`;

const PasswordBox = styled.div`
  // margin-top: -24px;
`

const HelperParagraph = () => (
  <div className="mail-to-help">
    혹시 id나 비밀번호를 잊었다면,{' '}
    <Link href="mailto:cntech0507@cntechsystems.com">
      아래 메일로 문의 바랍니다.
      <br />
      cntech0507@cntechsystems.com
    </Link>
  </div>
)

export default function Login() {
  const navigate = useNavigate();
  const auth = useAuth();

  const [values, setValues] = useState({
    user: sessionStorage?.LOGGED_TYPE || null,
    error: null,
    showPassword: false
  });
  
  const {
    register,
    handleSubmit,
    setError,
    clearErrors,
    formState: { errors, isSubmitting }
  } = useForm({
    defaultValues: { // fake username for test
      username: '',
      password: ''
    }
  });
  
  const onSubmit = async (data) => {
    clearErrors();    
    auth.signin(
      data,
      () => {
        navigate('/dashboard', { replace: true });
      },
      () => {
        setError('username', { type: 'accountCheck', message: '존재하지 않는 계정입니다. 입력한 정보를 다시 한 번 확인하세요.' })
        setError('password', { type: 'accountCheck', message: '' })
      }
    )
  }

  const handleClickShowPassword = () => setValues({
    ...values,
    showPassword: !values.showPassword,
  });

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  

  return (
    <StyledLoginBox>
      <Logo type="dark" size={24}/>
      <Typography variant="body1" sx={{ mt: '32px', mb: '60px' }}></Typography>
      <StyledForm onSubmit={handleSubmit(onSubmit)}>
        <TextField 
          id="username"
          name="username" // username for fakeAuthProvider
          fullWidth 
          autoFocus
          type="text"
          label="Id"
          InputLabelProps={{ shrink: true }}
          inputProps={{
            ...register('username', { required: '사용자 id를 입력하세요.' })
          }}
          error={Boolean(errors.username)}
          helperText={errors?.username && <span>{errors.username.message}</span>}
          disabled={isSubmitting}
        />
        <PasswordBox>
          <TextField 
            id="password"
            name="password"
            fullWidth
            type={values.showPassword ? 'text' : 'password'}
            label="Password"
            InputLabelProps={{ shrink: true }}
            inputProps={{
              ...register('password', {
                required: '비밀번호를 입력하세요.',
              })
            }}
            error={Boolean(errors.password)}
            helperText={errors?.password && <span>{errors.password.message}</span>}
            disabled={isSubmitting}
          />
          <FormControlLabel
            control={<Checkbox />}
            label="비밀번호 보이기"
            sx={{ ml: 0.4, mt: 1 }}
            checked={values.showPassword}
            disabled={isSubmitting} 
            onChange={handleClickShowPassword}
            onMouseDown={handleMouseDownPassword}
          />
        </PasswordBox>
        <Button type="submit" variant="contained" sx={{ width: '158px', ml: 'auto' }} disabled={isSubmitting} size="large">로그인</Button>
      </StyledForm>
      <HelperParagraph />
    </StyledLoginBox>
  );
};
