import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Divider from '@mui/material/Divider';
import ContentTitle from '../../components/Layout/ContentTitle';
import { MainCard } from '../../components/Card';
import { Chart, DEFAULT_OPTIONS } from '../../components/Chart';

/**
 * chart data form
 * [{
 *  name: 'SMC_ALGO',
 *  data: [...generateBarData(10)]
 * }, {
 *  name: 'KUMC_ALGO',
 *  data: [...generateBarData(10)]
 * }]
 */
const generateBarData = (length) => {
  return Array(length).fill(0).map((_,index) => Math.round(Math.random() * index) + 89)
}

const FILL_COLORS = [ '#4782DA', '#A4D927', '#5FD163', '#FEB019', '#38C3EA', '#FF7223' ];
const ALGORITHM_NAMES = ['응급실 중증환자 처방 추천 모델', '응급실 심혈관 시술 및 수술 예측 모델']

const StickChartEx = ({ id, title, index }) => {
  const options = {
    ...DEFAULT_OPTIONS,
    chart: {
      ...DEFAULT_OPTIONS.chart,
      id,
      type: 'bar',
      animations: {
        enabled: true,
        easing: 'linear',
        dynamicAnimation: {
          speed: 1000
        }
      },
    },
    title: {
      text: title,
      align: 'left'
    },
    xaxis: {
      type: 'category',
      tickPlacement: 'between',
      categories: Array(10).fill(0).map((_, idx) => String(idx + 1)),
      labels: {
        formatter: function (value) {
          return `${value} round`
        }
      }
    },
    yaxis: {
      min: 80,
      max: 100,
      tickAmount: 4,
    },
    colors: FILL_COLORS.slice(index, index + 1),
    legend: {
      position: 'top',
      horizontalAlign: 'left',
    },
  }
  const series = [{
    name: ALGORITHM_NAMES[index],
    data: generateBarData(10)
  }]
  return (
    <div id={`bar-${id}`}>
      <Chart
        type="bar"
        options={options}
        series={series}
        height={220}
      />
    </div>
  )
}

const LineChartEx = () => {
  const options = {
    ...DEFAULT_OPTIONS,
    chart: {
      ...DEFAULT_OPTIONS.chart,
      id: 'accuracy',
      type: 'line'
    },
    title: {
      text: '라운드별 정확도 비교',
      align: 'left',
    },
    xaxis: {
      type: 'category',
      tickPlacement: 'between',
      categories: Array(10).fill(0).map((_, idx) => String(idx + 1)),
      labels: {
        formatter: function (value) {
          return `${value} round`
        }
      }
    },
    yaxis: {
      min: 80,
      max: 100,
      tickAmount: 4,
    },
    colors: FILL_COLORS.slice(0, 2),
    markers: {
      size: 6,
    },
    legend: {
      show: true,
      position: 'bottom',
    }
  }
  const series = [{
    name: ALGORITHM_NAMES[0],
    data: generateBarData(10)
  }, {
    name: ALGORITHM_NAMES[1],
    data: generateBarData(10)
  }]
  return (
    <div>
      <Chart
        type="line"
        options={options}
        series={series}
        height={220}
      />
    </div>
  )
}

const TabPanel = (props) => {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Grid container spacing={2}>
          <Grid item xs={12} lg={3}>
            <MainCard sx={{ overflow: 'visible' }}>
              <StickChartEx
                id="acu"
                index={value}
                title={'정확도'}
              />
            </MainCard>
          </Grid>
          <Grid item xs={12} lg={3}>
            <MainCard sx={{ overflow: 'visible' }}>
              <StickChartEx
                id="rec"
                index={value}
                title={'재현율'}
              />
            </MainCard>
          </Grid>
          <Grid item xs={12} lg={3}>
            <MainCard sx={{ overflow: 'visible' }}>
              <StickChartEx
                id="pre"
                index={value}
                title={'정밀도'}
              />
            </MainCard>
          </Grid>
          <Grid item xs={12} lg={3}>
            <MainCard sx={{ overflow: 'visible' }}>
              <StickChartEx
                id="f1"
                index={value}
                title={'f1 score'}
              />
            </MainCard>
          </Grid>
          
        </Grid>
      )}
    </div>
  )
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
}


const a11yProps = (index) => ({
  'id': `dimple-tab-${index}`,
  'aria-controls': `simple-tabpanel-${index}`
})

const Dashboard = () => {
  const [value, setValue] = useState(0);
  const handleChange = (event, newValue) => setValue(newValue)
  
  return (
    <>
      <ContentTitle title="모델 현황" />
      <Box sx={{ mb: 2 }}>
        <Tabs value={value} onChange={handleChange}>
          {ALGORITHM_NAMES.map((name, index) => <Tab key={name} label={name} {...a11yProps(index)} />)}
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}></TabPanel>
      <TabPanel value={value} index={1}></TabPanel>
      <Divider />
      <div>
        <MainCard>
          <LineChartEx />
        </MainCard>
      </div>
    </>
  );
}

export default Dashboard;