import React from 'react';
import { Outlet } from 'react-router';
import { useInRouterContext } from 'react-router-dom';

import styled from '@emotion/styled';
import { useTheme } from '@mui/material/styles';
import { useMediaQuery } from '@mui/material';

import { useAuth } from '../../hooks/useAuth';
import { navItems } from '../../routes/navItems';
import HeaderFixed from './HeaderFixed';
import NavFixed from './NavFixed';
import MainContent from './MainContent';

import { NAV_WIDTH, HEADER_HEIGHT } from '../../constants/pixels.js';

const LayoutWrapper = styled.div(({ theme }) => ({
  [theme.breakpoints.up('md')]: {
    display: 'grid',
    gridTemplateColumns: `${NAV_WIDTH}px minmax(calc(100vw - ${NAV_WIDTH}px), auto)`,
    gridTemplateRows: `${HEADER_HEIGHT}px minmax(calc(100vh - ${HEADER_HEIGHT}px), auto)`,
    gridTemplateAreas: `
      'header header'
      'nav main'`,
    '& > header': {
      gridArea: 'header'
    },
    '& > nav': {
      gridArea: 'nav'
    },
    '& > main': {
      gridArea: 'main'
    },
  }
}));

export default function Layout() {
  let { user } = useAuth();
  const menus = navItems.map(group => ({
    ...group,
    items: group.items.filter(item => item.access.includes(user.type))
  }))
  
  const theme = useTheme();
  const RouterContext = useInRouterContext();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const mobileBreakpoint = 'md';
  const isMobileUp = useMediaQuery(theme.breakpoints.up(mobileBreakpoint));

  const handleDrawerToggle = () => setMobileOpen(!mobileOpen);

  const MobileNavProp = isMobileUp ? {
    open: mobileOpen,
    onClose: handleDrawerToggle
  } : {};

  const handleDragOver = (event) => {
    event.preventDefault();
    if (event?.dataTransfer) {
      if (event.target.closest('.file-picker-wrapper') === null
        && event.dataTransfer.effectAllowed !== 'none') {
        event.dataTransfer.dropEffect = 'none';
        event.dataTransfer.effectAllowed = 'none';
        return;
      }
      event.dataTransfer.dropEffect = 'copy';
      event.dataTransfer.effectAllowed = 'copy';
    }
  }

  const handleDrop = (event) => {
    if (event.target.closest('.file-picker-wrapper') === null) {
      event.preventDefault();
      return;
    }
  }

  return (
    <LayoutWrapper
      onDragOver={(e) => handleDragOver(e)}
      onDrop={(e) => handleDrop(e)}
    >
      <HeaderFixed onDrawerToggle={handleDrawerToggle} breakpoint={mobileBreakpoint} />
      <NavFixed 
        menus={menus}
        variant={isMobileUp ? "permanent" : "temporary"}
        {...MobileNavProp}
      />
      <MainContent>
        {RouterContext && <Outlet />}
      </MainContent>
    </LayoutWrapper>
  )
}