import React, { useState } from 'react';
import styled from '@emotion/styled';
import { css } from '@emotion/css';
import Alert from '@mui/material/Alert';
import Button from '@mui/material/Button';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import { useForm } from 'react-hook-form';

import ContentTitle from '../../components/Layout/ContentTitle';
import { HistoryDataTable } from '../../components/Table';
import { MainCard, SubCard } from '../../components/Card';
import { Input } from '../../components/Input';
import { Loader } from '../../components/Loader';
import Icon from '../../assets/icons';

import {
  // eslint-disable-next-line
  BASE_URL,
  useGetAllModelListQuery,
  useUploadNewFileMutation,
} from '../../service/models';
// import useNotifier from '../../hooks/useNotifier'
import { useSnackbar } from 'notistack';
import { useAuth } from '../../hooks/useAuth';
import { useNavigate } from 'react-router-dom';

const HorizontalStack = styled('div')`
  display: grid;
  row-gap: 16px;
  column-gap: 16px;
`;

const SelectBox = styled('div')`
  display: flex;
  margin-bottom: 1rem;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: nowrap;
  & > div:first-of-type {
    width: 300px;
    margin-right: 16px;
  }
  & > span {
    white-space: nowrap;
    margin-right: 8px;
  }
`;

export default function Upload() {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  let { user, session } = useAuth();
  const hos = user.type.toUpperCase()
  const initialAlgorithm = `${hos}_ALGO`;
  const [doneFetch, setDoneFetch] = useState(false);
  const [algorithm, setAlgorithm] = useState(initialAlgorithm);
  
  // eslint-disable-next-line
  const [uploadNewFile, { isLoading: isUploading, isSuccess: isUploadSuccess, isError: isUploadError, status }] = useUploadNewFileMutation();
  //,{..., status, isUninitialized, originalArgs, reset: Function }
  const { data, error, isLoading: isListLoading, isFetching: isListFetching, refetch } = useGetAllModelListQuery();
  
  const rows = data?.filter((el) => el.hos === hos) || [];
  const rowsByAlgo = (_algorithm) => rows.filter(({ algo }) => algo === _algorithm)

  const handleRefresh = () => {
    refetch();
  }

  const {
    register,
    handleSubmit,
    reset,
    clearErrors,
    formState: { errors, isDirty, isSubmitting }
  } = useForm();

  const onSubmit = async (data, e) => {
    e.preventDefault();
    setDoneFetch(false);
    
    const validSession = await session()
    console.log(validSession)

    if (!validSession.user) {
      alert('세션이 만료되었습니다. 재로그인 하시기 바랍니다.')
      navigate('/login', { replace: true })
      return;
    }
    
    const { file } = data;
    let formData = new FormData();
    formData.set("file", file[0]);
  
    const key = e.timeStamp;
  
    uploadNewFile({ algo: algorithm, body: formData })
      .then((response) => {
        //console.log('upload response',response)
        reset();
        setDoneFetch(true);
      })
      .then((data) => {
        console.log('response data: ', data);
        if (data) {
          enqueueSnackbar(
            data?.message || '파일 업로드가 완료되었습니다',
            {
              key,
              variant: 'success',
            });
        }
      })
      .catch((error) => {
        //console.log('error', error)
        setDoneFetch(true)
        enqueueSnackbar(
          error?.message || '파일 업로드에 실패하였습니다',
          {
            key,
            variant: 'error'
          });
      })

  };

  const onError = (e) => {
    const key = Date.now();
    enqueueSnackbar(
      e?.message || errors?.file.message || '전송에 실패하였습니다.',
      {
        key,
        variant: 'error'
      }
    )
  };

  if (error) return <div>error: {error}</div>;

  return (
    <>
      {/* -alert- */}
      {errors?.file && (
        <Alert 
          variant="filled" 
          severity="error" 
          closeText="Close" 
          sx={{ mb: 4 }}
          action={
            <IconButton onClick={() => clearErrors("file")}>
              <Icon.Cross />
            </IconButton>
          }
        >{errors.file.message}</Alert>
      )}
      
      <ContentTitle
        title="모델 업로드"
        pageAction={(
          <Button
            color="secondary"
            disabled={isListLoading || isListFetching}
            startIcon={(
              <span
                className={css`
                  margin-right: 0;
                  line-height: 0;
                  svg {
                    animation: ${(isListLoading || isListFetching) ? 'spin 1s ease-in-out infinite;' : 'none'}
                  }
                `}
              >
                <Icon.Refresh stroke={2} />
              </span>
            )}
            onClick={handleRefresh}
          >테이블 새로고침</Button>
        )}
      />
      <HorizontalStack>
        <MainCard title="파라미터 전송">
          <SelectBox>
            <Typography variant="subtitle2" component="span" color="primary.main">모델명</Typography>
            <Select
              value={algorithm}
              fullWidth
              size="small"
              color="primary"
              variant="standard"
              onChange={(event) => setAlgorithm(event.target.value)}
            >
              <MenuItem value="SMC_ALGO">응급실 중증환자 처방 추천 모델</MenuItem>
              <MenuItem value="KUMC_ALGO">응급실 심혈관 시술 및 수술 예측 모델</MenuItem>
            </Select>
          </SelectBox>
          <SubCard hasContent contentSX={{ textAlign: 'right' }}>
            {/* -file picker- */}
            {/* -change Loader below- */}
            <form
              method="post"
              encType="multipart/form-data"
              onSubmit={handleSubmit(onSubmit, onError)}
            >
              <Input.File
                register={register}
                name="file"
                allowMultiple={false}
                isLoading={isSubmitting || isUploading}
                submitted={doneFetch}
                reset={reset}
                accept="application/x-hdf5"
              />
              <br />
              <Button variant="contained" size="large" ml="auto" type="submit" disabled={!isDirty || isSubmitting}>전송</Button>
            </form>
          </SubCard>
        </MainCard>
        <MainCard>
          <SubCard>
            {/* history list */}
            {(isListLoading || isListFetching || !data)
              ? <Loader />
              : <HistoryDataTable
                tableId={algorithm}
                title="모델 업데이트 히스토리"
                bodyHeight="220px"
                rows={rowsByAlgo(algorithm)}
              />
            }
          </SubCard>
        </MainCard>
      </HorizontalStack>
    </>
  );
};