import React from 'react';
import { Navigate, useLocation, Outlet } from 'react-router-dom';
import { useAuth } from '../hooks/useAuth';
import { DefaultErrorPage } from '../components/Error/NotMatch';

const Unauthorized = () => {
  return <DefaultErrorPage errorMsg="접근할 수 없는 페이지입니다." />
}

export const Authorization = ({ permissions }) => {
  const { user } = useAuth();
  const location = useLocation();
  
  const isAllowed = permissions.some(allowed => user?.permissions.includes(allowed)) || [];
  if (user.type && isAllowed) {
    return <Outlet />
  }

  alert('접근할 수 없는 페이지입니다.')
  
  return <Navigate to="/login" state={{ path: location.pathname }} />
}