import { forwardRef } from 'react';
import PropTypes from 'prop-types';
import { Box, Typography, InputBase, LinearProgress } from '@mui/material';
import { useTheme } from '@mui/material/styles';

import Icon from '../../../assets/icons';
import { styleVisuallyHidden, styleAbsolutePosition } from '../../../utils';

export const FileInput = forwardRef((props, ref) => {
  const { visuallyHidden, allowMultiple, minH, accept, onInput, ...others } = props;
  const theme = useTheme();
  const multiple = allowMultiple;
  const minHeight = minH; ///(px|r?em|v(h|w)|%)/g.test(minH) ? minH : null;
  return (
    <Box
      className={visuallyHidden ? "hidden file-picker-wrapper" : "file-picker-wrapper"}
      sx={fileInputStyle(minHeight, theme)}
      ref={ref}
    >
      <InputBase 
        type="file"
        multiple={multiple}
        sx={{
          ...styleAbsolutePosition,
          width: "100%",
          height: "100%",
          "& input[type=file]": {
            padding: 0,
            display: "block",
            width: "100%",
            height: "100%",
            opacity: 0,
            cursor: "pointer",
          },
        }}
        inputProps={{ accept: accept || null, multiple, "aria-label": "드래그 하거나 클릭하여 파일 업로드" }}
        // onChange={(event) => console.dir(event.target.value)}
        onInput={onInput}
        // ...register([name]), ...
        {...others}
      />
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          width: "2.4rem",
          height: "2rem",
        }}
      >
        <Icon.Plus color={theme.palette.grey[500]} />
      </Box>
      <Typography variant="body2">
        드래그 하거나 클릭하여 파일 업로드
      </Typography>
    </Box>
  )
});

FileInput.propTypes = {
  type: PropTypes.oneOf([ 'single', 'multiple' ]),
  minH: function(props, propName, componentName) {
    if (!/(px|r?em|v(h|w)|%)/g.test(props[propName])) {
      return new Error(`Invalid prop ${propName} supplied to \`${componentName}\`. Validation failed.`)
    }
  }
};

FileInput.defaultProps = {
  minH: '5rem',
  allowMultiple: false
}


export const FileProgressbar = ({ file, done, ...props }) => {
  return (
    <Box>
      <Box
        sx={{
          mb: 1,
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        <Typography
          component='p'
          variant='subtitle1'
        >{file.name}</Typography>
        {/* <Typography
          component='p'
          variant='subtitle1'
        >{Math.round(progress)}%</Typography> */}
      </Box>
      <Box>
        <LinearProgress {...props} />
      </Box>
    </Box>
  )
}

function fileInputStyle(minHeight, theme) {
  return {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
    width: '100%',
    height: '100%',
    minHeight: minHeight,
    padding: 1,
    position: 'relative',
    overflow: 'hidden',
    background: theme.palette.grey[100],
    border: `1px solid ${theme.palette.grey[300]}`,
    borderRadius: '4px',
    '&:hover, &:focus-visible': {
      background: theme.palette.grey[300]
    },
    '&.hidden': styleVisuallyHidden,
  };
}
