import { /* current, */ createSlice } from '@reduxjs/toolkit';

const initialState = [];

export const notificationsSlice = createSlice({
  name: 'notifications',
  initialState,
  reducers: {
    snackbarEnqueue: (state, action) => {
      // console.log('+', current(state));
      // console.log('+', action);
      state.push({
        key: action.payload.key,
        ...action.payload.notification,
      })
    },
    snackbarClose: (state, action) => {
      state = state.map(notification => (
                (action.payload.dismissAll || notification.key === action.payload)
                  ? { ...notification, dismissed: true }
                  : { ...notification }
              ))
    },
    snackbarRemove: (state, action) => {
      // console.log('-', current(state))
      // console.log('-', action)
      return state.filter(notification => notification.key !== action.payload)
    }
  }
})

export const { snackbarEnqueue, snackbarClose, snackbarRemove } = notificationsSlice.actions;

export default notificationsSlice.reducer;


