/** 
 * ELF data table header component (column names)
 * @param {*} props 
 * @returns Customized TableHead
 */
function HistoryDataTableHead(props) {
  const {
    headCells,
    onSelectAllClick,
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort,
    checkboxSelection
  } = props;
  const createSortHandler = (property) => (event) => {
    onRequestSort(event, property);
  };

  const headCellMapped = headCells.map(option => option?.width ? `${option.width}px` : 'auto')
  const templateColumns = checkboxSelection ? ['48px'].concat(headCellMapped) : [].concat(headCellMapped)

  return (
    <TableHead>
      <TableRow 
        sx={{
          display: 'grid',
          gridTemplateColumns: templateColumns.join(' '),
        }}
      >
        {checkboxSelection && (
          <TableCell padding="checkbox">
            <Checkbox
              color="primary"
              indeterminate={numSelected > 0 && numSelected < rowCount}
              checked={rowCount > 0 && numSelected === rowCount}
              onChange={onSelectAllClick}
              inputProps={{
                'aria-label': 'select all desserts',
              }}
            />
          </TableCell>
        )}
        {headCells.map((headCell) => (
          <TableCell
            key={headCell.name}
            align="left"
            padding={headCell.disablePadding ? 'none' : 'normal'}
            sortDirection={orderBy.name === headCell.name ? order : false}
          
          >
            <TableSortLabel
              active={orderBy.name === headCell.name}
              direction={orderBy.name === headCell.name ? order : 'asc'}
              onClick={createSortHandler({ name: headCell.name, type: headCell.type })}
            >
              {headCell.label}
              {orderBy.name === headCell.name ? (
                <Box component="span" sx={styleVisuallyHidden}>
                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                </Box>
              ) : null}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

HistoryDataTableHead.propTypes = {
  headCells: PropTypes.array.isRequired,
  numSelected: PropTypes.number.isRequired,
  order: PropTypes.oneOf(['asc', 'desc']).isRequired,
  orderBy: PropTypes.shape({
    name: PropTypes.string.isRequired,
    type: PropTypes.oneOf([ 'number', 'date', 'string' ]).isRequired,
  }).isRequired,
  rowCount: PropTypes.number.isRequired,
  onRequestSort: PropTypes.func.isRequired,
  onSelectAllClick: PropTypes.func.isRequired,
  checkboxSelection: PropTypes.bool.isRequired,
};

/**
 * View table title or selected row(s) count or table action button(optional)
 * @param {*} props 
 * @returns Table Toolbar
 */
const HistoryDataTableToolbar = (props) => {
  const { title, numSelected, ...other } = props;

  return (
    <Toolbar
      sx={{
        pl: { sm: 2 },
        pr: { xs: 1, sm: 1 },
        borderBottom: '1px solid rgba(224, 224, 224, 1)',
        bgcolor: (theme) => theme.palette.background.default,
        ...(numSelected > 0 ? {
          color: (theme) =>
            theme.palette.primary.main,
        } : {
          color: (theme) => theme.palette.background.default,
        }),
      }}
    >
      {numSelected > 0 ? (
        <Typography
          sx={{ flex: '1 1 100%', fontWeight: 500 }}
          color="inherit"
          variant="subtitle1"
        
        >
          {numSelected} selected
        </Typography>
      ) : (
        <Typography
          sx={{ flex: '1 1 100%', fontWeight: 500 }}
          variant="h6"
          id="tableTitle"
        
        >
            {title}
        </Typography>
      )}

      {other?.tableAction || null}
    </Toolbar>
  );
};

HistoryDataTableToolbar.propTypes = {
  title: PropTypes.string.isRequired, 
  numSelected: PropTypes.number.isRequired,
};

const EmptyBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 20px;
  overflow: hidden;
`

/**
 * ELF model history data table
 * - Table Toolbar
 * - Table Container
 *    - Table Head
 *    - Table Body
 * @param {*} props 
 * @returns Whole Table with data
 */
function HistoryDataTable(props) {
  const {
    tableId,
    title,
    bodyHeight,
    rows,
    headCells,
    setSelected,
    selected = [],
    checkboxSelection,
    ...other
  } = props;

  const initialState = {
    order: 'asc',
    orderBy: {
      name: 'date',
      type: 'date', // 'number' | 'date' | 'string'
    }
  }
  
  const [order, setOrder] = useState(initialState.order);
  const [orderBy, setOrderBy] = useState(initialState.orderBy);
  const [tableRows, setTableRows] = useState(
    rows.map((row, idx) => ({
      id: idx + 1,
      name: row.name,
      updatedDate: row.date,
      volume: row.size,
      accuracy: row.acu,
    }))
  );

  useEffect(() => {
    setTableRows(
      rows.map((row, idx) => ({
        id: idx + 1,
        name: row.name,
        date: row.date,
        size: row.size,
        acu: row.acu,
        rec: row.rec,
        pre: row.pre,
        f1: row.f1,
      }))
    )
  }, [tableId, rows]);
  
  const handleRequestSort = (event, property) => {
    const isAsc = orderBy.name === property.name && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (checkboxSelection === false) return;

    if (event.target.checked) {
      setSelected(tableId, tableRows);
      return;
    }
    setSelected(tableId, []);
  };

  const handleItemSelect = (event, row) => {
    if (checkboxSelection === false) return;
    
    const { id } = row;
    const selectedIndex = selected.findIndex(item => item.id === id);
    let newSelected = [];

    if (selectedIndex === -1) {
      // unchecked => checked
      newSelected = newSelected.concat(selected, row);
      
    } else {
      // checked => unchecked
      newSelected = selected.filter(selected => selected.id !== id)
    }
    /* => another way to get newSelected  
    if (selectedIndex === -1) {
      // `id`가 selected에 존재하지 않는 경우 (unchecked => checked)
      newSelected = newSelected.concat(selected, id);
      
    } else if (selectedIndex === 0) {
      // `id`가 selected의 가장 앞에 있는 경우
      console.log('selectedIndex === 0')
      newSelected = newSelected.concat(selected.slice(1));
      
    } else if (selectedIndex === selected.length - 1) {
      // `id`가 selected의 마지막에 있는 경우
      console.log('selectedIndex === selected.length - 1')
      newSelected = newSelected.concat(selected.slice(0, -1));

    } else if (selectedIndex > 0) {
      // 그 외 `id`가 selected에 존재하는 경우
      console.log('selectedIndex > 0')
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }
    */
    
    setSelected(tableId, newSelected);
  };

  const selectedIndexes = selected.map(selected => selected.id)
  const isSelected = (id) => selectedIndexes.indexOf(id) !== -1;

  /* if you need to support IE11, you can use `stableSort(:48)` call with:
     stableSort(tableRows, getComparator(order, orderBy)) */
  const sortedTableRows = tableRows.slice().sort(getComparator(order, orderBy));

  return (
    <Box sx={{ width: '100%', overflowY: 'auto' }}>
      <Paper sx={{ width: '100%' }} elevation={0}>
        <HistoryDataTableToolbar
          title={title}
          tableId={tableId}
          numSelected={selected.length}
          {...other?.toolbar || {}}
        />
        <TableContainer sx={{ maxHeight: bodyHeight ? `calc(${bodyHeight} + 120px)` : '100%', overflowY: 'auto' }}>
          <Table
            stickyHeader
            size="medium"
            aria-labelledby="tableTitle"
            // sx={{ minWidth: 750 }}
          >
            <HistoryDataTableHead
              headCells={headCells}
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              rowCount={rows.length}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              checkboxSelection={checkboxSelection}
              {...other?.head || {}}
            />
            <TableBody>
              {(rows.length > 0 && sortedTableRows.length > 0)
              ? sortedTableRows.map((row, index) => {
                  const isItemSelected = isSelected(row.id);
                  const labelId = `enhanced-table-checkbox-${index}`;

                  return (
                    <TableRow
                      hover
                      onClick={(event) => handleItemSelect(event, row)}
                      role="checkbox"
                      aria-checked={isItemSelected}
                      tabIndex={-1}
                      key={row.id}
                      selected={isItemSelected}
                      sx={{
                        display: 'grid',
                        gridTemplateColumns: checkboxSelection 
                          ? ['48px'].concat(headCells.map(option => option?.width ? `${option.width}px` : 'auto')).join(' ')
                          : [].concat(headCells.map(option => option?.width ? `${option.width}px` : 'auto')).join(' '),
                        '&:last-of-type': {
                          borderBottom: '0px solid',
                        }
                      }}
                    >
                      {checkboxSelection && (
                        <TableCell padding="checkbox">
                          <Checkbox
                            color="primary"
                            checked={isItemSelected}
                            inputProps={{
                              'aria-labelledby': labelId,
                            }}
                          />
                        </TableCell>
                      )}
                      <TableCell
                        id={labelId}
                        scope="row"
                      >
                        {row.name}
                      </TableCell>
                      <TableCell>{row.date}</TableCell>
                      <TableCell
                        title={`${new Intl.NumberFormat('ko').format(row.size)} (byte)`}
                      >{headCells[2].valueFormatter(row.size)}</TableCell>
                      <TableCell>
                        {row.acu}
                      </TableCell>
                      <TableCell>
                        {row.rec}
                      </TableCell>
                      <TableCell>
                        {row.pre}
                      </TableCell>
                      <TableCell>
                        {row.f1}
                      </TableCell>
                    </TableRow>
                  )
                })
              : <EmptyBox>- No Data -</EmptyBox>}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </Box>
  );
};

HistoryDataTable.propTypes = {
  tableId: PropTypes.oneOfType([
    PropTypes.oneOf(["CNT", "SMC", "KUMC"]),
    PropTypes.string
  ]).isRequired,
  title: PropTypes.string.isRequired,
  bodyHeight: function(props, propName, componentName) {
    if (typeof props[propName] !== 'string') {
      return new Error(`Invalid value of ${propName} supplied to \`${componentName}\`. Validation failed.`)
    }
    if (!/(px|r?em|v(h|w)|%)/g.test(props[propName])) {
      return new Error(`Invalid prop ${propName} supplied to \`${componentName}\`. Validation failed.`)
    }
  },
  rows: PropTypes.array,
  headCells: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string.isRequired,
    numeric: PropTypes.bool, // default: false
    type: PropTypes.oneOf([ 'number', 'date', 'string' ]),
    disablePadding: PropTypes.bool, // default: false
    label: PropTypes.string.isRequired,
    width: PropTypes.number.isRequired,
    valueFormatter: PropTypes.func,
  })),
  checkboxSelection: PropTypes.bool,
  selected: PropTypes.array,
  setSelected: function (props, propName, componentName) {
    if (props['checkboxSelection'] && !props[propName]) {
      return new Error(`The \`${componentName}\` must have ${propName} property.`)
    }
    if (props['checkboxSelection'] && typeof props[propName] !== 'function') {
      return new Error(`Invalid value of ${propName} supplied to \`${componentName}\`. Validation failed.`)
    }
  },
};

HistoryDataTable.defaultProps = {
  rows: [],
  headCells: historyDataHeadRow,
  bodyHeight: '',
  checkboxSelection: false,
};

export default HistoryDataTable;