// Fake async function
export const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

export const createData = (data, keys) => {
  if (!Array.isArray(data) || !Array.isArray(keys)) {
    throw new Error('Both of `data` and `keys` props must be flatten array.');
  };
  if (data.length !== keys.length) {
    throw new Error('This `data` and `keys` props must have same length.');
  };
  return keys.reduce(
    (prev, key) => ({
      ...prev,
      [key]: data[keys.indexOf(key)]
    }),
    {}
  );
};

export const styleAbsolutePosition = {
  display: 'block',
  position: "absolute",
  top: 0,
  left: 0,
}

export const styleVisuallyHidden = {
  ...styleAbsolutePosition,
  zIndex: -1,
  left: '-1px',
  top: '-1px',
  width: '1px',
  height: '1px',
  margin: 0,
  padding: 0,
  clip: 'rect(0px, 0px, 0px, 0px)',
  border: '0px solid',
  overflow: 'hidden',
  visibility: 'hidden',
  whiteSpace: 'nowrap',
  backgroundColor: 'transparent',
};