import Icon from '../assets/icons';

export const accessControl = {
  'cnt': ['/dashboard', '/averaging'],
  'smc': ['/dashboard', '/upload', '/download'],
  'kumc': ['/dashboard', '/upload', '/download'],
}

const getAccess = (path) => Object.entries(accessControl).filter(([k,v]) => v.includes(path)).map(([k,v]) => k)

