import React from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';

import { PERMISSIONS } from '../permissions/Permissions';
import { Authorization } from './Authorization';
import { RequireAuth } from '../hooks/useAuth';
import Layout from '../components/Layout';
import Login from '../view/Login';
import * as Model from '../view/Model';
import * as Error from '../components/Error';

const AppAuthLayout = () => {
  return (
    <RequireAuth>
      <Layout />
    </RequireAuth>
  )
}


const ServiceRoute = () => {
  return (
    <Routes>
      <Route path="/" element={<AppAuthLayout />}>
        <Route index element={<Navigate to="/dashboard" replace={true} />} />
        <Route path="dashboard" element={<Model.Dashboard />} />
        <Route element={<Authorization permissions={[PERMISSIONS.CAN_VIEW_GLOBAL]} />}>
          <Route path="averaging" element={<Model.Averaging />} />
        </Route>
        <Route element={<Authorization permissions={[PERMISSIONS.CAN_VIEW_LOCAL]} />}>
          <Route path="upload" element={<Model.Upload />} />
          <Route path="download" element={<Model.Download />} />
        </Route>
      </Route>
      <Route path="/login" element={<Login />} />
      <Route path="/*" element={<Error.NotMatch />} />
    </Routes>
  );
}

export default ServiceRoute;
