import { createSlice } from '@reduxjs/toolkit'

const initialState = {};

export const progressSlice = createSlice({
  name: 'progress',
  initialState,
  reducers: {
    progressReset: (state, action) => {
      state = initialState;
    },
    progressSet: (state, action) => {
      for (let [key, value] of Object.entries(action)) {
        state[key] = value;
      }
    }
  }
});

export const { progressReset, progressSet } = progressSlice.actions;

export default progressSlice.reducer;