import { Fade } from '@mui/material';
import { useTheme, alpha } from '@mui/material/styles';
import { keyframes } from '@emotion/css';
import { SnackbarProvider as Provider } from 'notistack';

import Icon from '../../assets/icons';

const progressLeak = keyframes`
  from { width: 100% }
  to { width: 0% }
`;

/* 
 * Set default options for snackbar
 */
export default function SnackbarProvider({ children }) {
  const theme = useTheme();
  const TIMEOUT = theme.transitions.duration.complex * 10; //3750 (ms)
  return (
    <Provider
      maxSnack={3} 
      iconVariant={{
        success: <Icon.Success stroke="2" />,
        error: <Icon.Error stroke="2" />,
        warning: <Icon.Warning stroke="2" />,
        info: <Icon.Info stroke="2" />,
      }}
      classes={{
        variantSuccess: 'Mui-notistack-success',
        variantError: 'Mui-notistack-error',
        variantWarning: 'Mui-notistack-warning',
        variantInfo: 'Mui-notistack-info',
      }}
      sx={{
        borderRadius: '8px',
        overflow: 'hidden',
        '& .SnackbarContent-root': {
          paddingRight: '20px',
          background: alpha(theme.palette.background.dimmed, 1),
          '&::after': {
            content: "''",
            display: 'block',
            width: '0%',
            height: '4px',
            position: 'absolute',
            left: '0',
            bottom: '0',
            background: alpha(theme.palette.grey[900], 0.3),
            backdropFilter: 'brightness(96%) saturate(160%)',
            animation: `${progressLeak} ${TIMEOUT}ms linear`
          },
          '.SnackbarItem-action': {
            'button, svg': {
              color: alpha(theme.palette.background.paper, 0.64)
            }
          },
          '.SnackbarItem-message svg': {
            marginRight: '8px'
          }
        },
        '& .Mui-notistack-success': {
          background: theme.palette.success.main,
          '&::after': {
            background: alpha(theme.palette.success.dark, 0.8),
          }
        },
        '& .Mui-notistack-error': {
          background: theme.palette.error.main,
          '&::after': {
            background: alpha(theme.palette.error.dark, 0.8),
          }
        },
        '& .Mui-notistack-warning': {
          background: theme.palette.warning.main,
          '&::after': {
            background: alpha(theme.palette.warning.dark, 0.8),
          }
        },
        '& .Mui-notistack-info': {
          background: theme.palette.info.main,
          '&::after': {
            background: alpha(theme.palette.info.dark, 0.8),
          }
        },
      }}
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'center',
      }}
      autoHideDuration={TIMEOUT}
      TransitionComponent={Fade}
    >
      {children}
    </Provider>
  )
}