import { useState, memo, forwardRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Box } from '@mui/material';

import { FileInput, FileProgressbar } from './FileInput';
import FileCard from './FileCard';
// import { FileUploader } from '../Inputs';
// import useForms from '../../../hooks/useForms';

const FileUploader = ({ register, name, ...rest }) => {
  if (!register) return <FileInput name={name} {...rest} />
  return <FileInput {...register(name)} {...rest} />
}
FileUploader.propTypes = {
  register: PropTypes.func,
  name: function (props, propName, componentName) {
    // name property is required with register
    if (props['register'] && (!props[propName] || typeof props[propName] !== 'string')) {
      return new Error(`With \`register\` property, The \`${componentName}\` must have ${propName} property.`)
    }
  },
}

export const FilePicker = memo(forwardRef((props, ref) => {
  const { allowMultiple, isLoading, submitted, reset, ...otherProps } = props;
  const [files, setFiles] = useState([]);

  useEffect(() => {
    if (!isLoading && submitted) {
      setFiles([])
    }
  }, [isLoading, submitted]);
  
  const hasFile = (f) => f && f.length > 0;
  const onInput = (event) => {
    event.preventDefault();
    const _files = event.target.files || event.originalEvent?.dataTransfer.files;
    if (hasFile(_files)) {
      setFiles(Object.values(_files));
    }
  };

  const handleDeleteSelectedFile = (event) => {
    const thisId = event.target.closest(".selected-file__item-card").id;
    const thisIndex = thisId.substring(thisId.length - 1);
    setFiles(
      [...files].filter((file, index) => {
        return parseInt(index, 10) !== parseInt(thisIndex, 10);
      })
    )
    if (allowMultiple === false) reset();
  };

  const handleLoadingDone = (doneFile) => {
    setFiles(
      [...files].filter(file => file.lastModified !== doneFile.lastModified)
    );
  }

  // * empty input => inputted file => form data submitting(loading) after submit btn click => fetching done (submitted)
  // ----------------------------------------------------
  // | state			|	hasFile	|	isSubmitting	| submitted	|
  // |--------------------------------------------------|
  // | empty			|		 X		| 			X				|			X			|
  // | inputted	  | 	 O		| 			X				| 		X			|
  // | loading		| 	 O		| 			O				| 		X			|
  // | submitted	| 	 X 		| 			X				|			O			|
  // ----------------------------------------------------
  const FileStatePresentation = isLoading
    ? files.map(file => <FileProgressbar key={file.lastModified} done={handleLoadingDone} file={file} />)
    : (
      <>
        <FileUploader visuallyHidden={allowMultiple !== true && hasFile(files)} allowMultiple={allowMultiple} {...otherProps} onInput={onInput} />
        {hasFile(files) && (
          Object.values(files).map((file, index) => (
            <FileCard
              key={`${file.lastModified}${index}`}
              id={`${file.lastModified}${index}`}
              filename={file.name}
              file={file}
              onDelete={handleDeleteSelectedFile}
            />
          ))
        )}
      </>
    )

  return (
    <Box ref={ref}>
      {/* file selector input */}
      {FileStatePresentation}
    </Box>
  );
}));

FilePicker.propTypes = {
  allowMultiple: PropTypes.bool,
  accept: PropTypes.string,
  minH: PropTypes.string,
  onInput: PropTypes.func
};

FilePicker.defaultProps = {
  allowMultiple: false,
  onInput: (event) => event.preventDefault()
};
