import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'
// https://www.npmjs.com/package/uuid
// import { v1 as uuidV1 } from 'uuid'

// import { snackbarEnqueue } from '../store/notificationsSlice'
// import { progressSet } from '../store/progressSlice'

/** 
 * <BASE_URL> 
 * ⬇ Set base url of api to get model list and upload file.
 */
// export const BASE_URL = 'http://localhost:3000'; // 'http://localhost:16000'; // local json-server for ui test
export const BASE_URL = 'http://34.64.42.98';

/**
 * `createApi`
 * API Reference: https://redux-toolkit.js.org/rtk-query/api/createApi
  */
export const modelApi = createApi({
  reducerPath: 'modelApi',
  baseQuery: fetchBaseQuery({ baseUrl: `${BASE_URL}/` }),
  // cache tag
  // https://redux-toolkit.js.org/rtk-query/usage/automated-refetching
  tagTypes: ['List'],
  endpoints: (builder) => ({
    /** 
     * ENDPOINT_DEFINITION
     * [name]: builder.query||mutation({
     *    query | queryFn,
     *    transformResponse?|,
     *    extraOptions?,
     *    providesTags||invalidatesTags?,
     *    keepUnusedDataFor?||,
     *    onQueryStarted?,
     *    onCacheEntryAdded?,
     * })
     */
    getAllModelList: builder.query({
      query: () => `files`,
      providesTags: (result) => result
        ? [
          ...result.map(({ id }) => ({ type: 'List', id }))
        ]
        : [{ type: 'List', id: 'LIST' }],
    }),
    uploadNewFile: builder.mutation({
      query: ({ algo, body }) => ({
        url: `upload_${algo}`,
        method: 'POST',
        body
      }),
      invalidatesTags: (result, error, { algo }) => [{ type: 'List', algo }]
    }),
    /* averagingModel: builder.query({
      // (ex) `averaging?Hos1=SMC&Name1=9767.h5&Hos2=KUMC&Name2=9767.h5`
      query: (querystring) => `averaging?${querystring}`,
      //+ async onQueryStarted(querystring, { dispatch, queryFulfilled }) { ... }
      //- transformResponse: (response, meta, arg) => ({response, meta, arg}),
    }) */
  })
})

// By camelCase: `use` + {{ KEYS_OF_ENDPOINT_DEFINITION }} + {{ ENDPOINT_TYPE }}
// (ex) useAddPostMutation
export const {
  useGetAllModelListQuery,
  useUploadNewFileMutation,
  // useAveragingModelQuery,
} = modelApi